<template>
    <div>
        <div id="container" style="height:100vh;width:100vw;"></div>
    </div>
</template>
<script>
// import PANOLENS from 'panolens'
export default{
    mounted() {
        setTimeout(() => {
            var panorama, panorama2, viewer, container, infospot;

                container = document.querySelector( '#container' );

                panorama = new PANOLENS.ImagePanorama( 'https://pchen66.github.io/Panolens/examples/asset/textures/equirectangular/tunnel.jpg' );
                panorama2 = new PANOLENS.ImagePanorama( 'https://pchen66.github.io/Panolens/examples/asset/textures/equirectangular/sunset.jpg' );
                infospot = new PANOLENS.Infospot( 500, PANOLENS.DataImage.Info );
                // console.log('info','https://gis.egov.balangankab.go.id/files/arrow.png')
                infospot.position.set( -100, -500, -5000 );
                infospot.addHoverText( "The Story" );
                // infospot.addEventListener( 'click', function(){
                //     viewer.setPanorama( panorama2 );
                // } );
                infospot.addEventListener('click', function (e) {
                    // this.hide();
                    var tweenScale = new TWEEN.Tween({x: 0, y: 0})
                        .to({ x: -1, y: 4, z: 4 }, 500)
                        .easing(TWEEN.Easing.Exponential.Out)
                        .onUpdate(function (nVal) {
                            this.parent.scale.set(nVal.x, nVal.y, nVal.z);
                        }.bind(this))
                        .onComplete(function (e) {
                            viewer.setPanorama(panorama2);
                        })
                        .start();
                }.bind(infospot));
                // panorama.addEventListener('leave-complete', function (e) {
                //     this.scale.set(-1, 1, 1);
                // }.bind(imagePanorama));
                panorama.add( infospot );

                // viewer = new PANOLENS.Viewer( { container: container } );
                viewer = new PANOLENS.Viewer( { container: container, autoRotate: true, autoRotateSpeed: 1, autoRotateActivationDuration: 5000 } );
                viewer.add( panorama, panorama2 );
                console.log(viewer)

                viewer.addUpdateCallback(function(){
                
                });
        }, 3000);
    },
    metaInfo: {
        script: [
            {
                src: "https://gis.egov.balangankab.go.id/files/three.min.js"
            },
            {
                src: "https://gis.egov.balangankab.go.id/files/panolens.js"
            },
        ]
    }
}
</script>